import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, AbstractControl } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { VehicleService } from 'app/shared/services/master/vehicle/vehicle.service';
import { Router } from '@angular/router';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { ClientService } from 'app/shared/services/client/client.service';
import { CountriesService } from 'app/shared/services/countries.service';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { MessageConstant} from 'app/shared/utils/message.constant';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
@Component({
  selector: 'app-action-dialog',
  templateUrl: './action-dialog.component.html',
  styleUrls: ['./action-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class ActionDialogComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  localStorage: any;
  status = false;
  filteredOptionCountry: Observable<any[]>;
  filteredOptionState: Observable<any[]>;
  filteredOptionCity: Observable<any[]>;
  countries: any;
  stateName: any;
  cities: any;
  states: any;
  pin_code_id: any;
  country_id: any;
  state_id: any;
  @ViewChild("toggleElement") ref: any;
  errorMsg:String;
  splitted:String;
  constructor(private fb: FormBuilder, private clientService: ClientService, private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<ActionDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, public countryService: CountriesService) { }

  ngOnInit() {
    this.initialize();
  }

  initialize() {

    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.params = this.data;

    this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.form = this.fb.group({
      name: ['', [Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      cp_name: ['', [Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      description: [''],
      addressLine1: [''],
      addressLine2: [''],
      landmark: [''],
      city: ['', [autocompleteObjectValidator(),Validators.required]],
      state: ['', [autocompleteObjectValidator(), Validators.required]],
      country: ['', [autocompleteObjectValidator(), Validators.required]],
      pincode:['', [Validators.required ,Validators.pattern('^[1-9]{1}[0-9]{2}\\s{0,1}[0-9]{3}$')]], 
      logoPath: [''],
      mobile : ['', [Validators.required ,Validators.pattern('^((\\+91-?)|0)?[0-9]{10}$')]] ,
      email: ['', [Validators.required, Validators.pattern('^[^\\s@]+@[^\\s@]+\\.[^\\s@]{2,}$')]],
      gst_number:['',[Validators.pattern('\^[0-9]{2}[a-z|A-Z]{5}[0-9]{4}[a-z|A-Z]{1}[0-9|a-z|A-Z]{1}[a-z|A-Z]{1}[0-9|a-z|A-Z]{1}$')]],
      tin_number:['',[Validators.pattern('\^[0-9]{11}$')]],
    });

    if (this.isEditMode) {
      this.assignValues(this.params);
    }
    //this.getCountry();
    this.getAllCountry();
    this.getAllState(this.params.data.country_id);
    this.getAllCity(this.params.data.state_id);
  }
  getAllCountry() {
    this.countryService.getAllCountry().subscribe(
      (data: any) => {
        //;
        this.countries = data;
        if (this.isEditMode) {
          //;
          const toSelect = this.countries.find(c => c.id == this.params.data.country_id);
          this.form.get('country').setValue(toSelect);

          this.filteredOptionCountry = this.form.controls.country.valueChanges
            .pipe(
              startWith(''),
              //tap(() => this.form.controls.chargingModel.setValue(toSelect)),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCountry(name) : this.countries)
            );
        }
      }, (err) => {

      }
    )
  }

  getAllState(countryID){
    this.countryService.getStateByCountry(countryID).subscribe(
      (data:any)=>{
        //;
        this.states = data;
        if(this.isEditMode){
          //;
          const toSelect = this.states.find(c => c.id == this.params.data.state_id);
          this.form.get('state').setValue(toSelect);
         
          this.filteredOptionState = this.form.controls.state.valueChanges
           .pipe(
          startWith(''),
          //tap(() => this.form.controls.chargingModel.setValue(toSelect)),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterState(name) : this.states)
        );
        }
        
         },(err)=>{
           
         }
    )
  }
  
  getAllCity(stateID){
    //this.getCitiesByStatesOnInit(state_id);
    this.countryService.getCityByState(stateID).subscribe(
      (data:any)=>{
        //;
        this.cities = data;
        if(this.isEditMode){
          //;
          const toSelect = this.cities.find(c => c.id == this.params.data.city_id);
          this.form.get('city').setValue(toSelect);
         
          this.filteredOptionCity = this.form.controls.city.valueChanges
           .pipe(
          startWith(''),
          //tap(() => this.form.controls.chargingModel.setValue(toSelect)),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCity(name) : this.cities)
        );
        }
        
         },(err)=>{
           
         }
    )
  }

  close() {
    this.dialogRef.close();
  }

  onYesClick() {
    if(this.form.valid){
      let formValues = this.form.value;

      const params: any = {
  
        name: formValues.name,
        cp_name: formValues.cp_name,
        description: formValues.description,
  
        logoPath: formValues.logoPath,
        mobile: formValues.mobile,
        email: formValues.email,
        address1: formValues.addressLine1,
        address2: formValues.addressLine2,
        PIN: formValues.pincode,
        landmark: formValues.landmark,
        city_id: formValues.city.id,
        state_id: formValues.state.id,
        country_id: formValues.country.id,
        gst_no:formValues.gst_number,
        tin_no: formValues.tin_number,
        status: this.status == true ? 'Y' : 'N'
        // tslint:disable-next-line: semicolon
      }
      // tslint:disable-next-line: no-//
  
      if (this.isEditMode) {
        params.id = this.params.data.id,
          params.modify_by = this.localStorage.userId;
      } else {
        params.created_by = this.localStorage.userId;
      }
  
      if (this.isEditMode) {
         this.clientService.update(params).subscribe(
           (resp: any) => {
              
             if(resp.status == true){
              this.snackBar.open(MessageConstant.recordUpdateMessage, 'Dismiss', { duration: 5000 });
              this.dialogRef.close();
             }else{
              if(resp.err_code == "ERROR : ER_DUP_ENTRY"){
                this. errorMsg = resp.message;
                var index=  this. errorMsg.lastIndexOf(" ");
                this.splitted = this. errorMsg.substring(index+1); 
                if(this.splitted == "'name'" ){
                  this.snackBar.open(MessageConstant.clientDuplicate, 'Dismiss', { duration: 5000 });
                }if(this.splitted == "'mobile'"){
                  this.snackBar.open(MessageConstant.mobileDuplicateClient, 'Dismiss', { duration: 5000 });
                }if(this.splitted == "'email'"){
                  this.snackBar.open(MessageConstant.emailDuplicateClient, 'Dismiss', { duration: 5000 });
                }
              }else{
                this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
              }
              this.dialogRef.close();
             }
            
           },
           (err) => {
             this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
           }
         )
      } else {
        this.clientService.create(params).subscribe(
          (data: any) => {
            this.snackBar.open(MessageConstant.clientCreate, 'Dismiss', { duration: 5000 });
            this.dialogRef.close();
          },
          (err) => {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )
  
      }
  
      this.dialogRef.close(params);
    }else{
      this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
    }
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  assignValues(params) {

    this.form.get('name').setValue(params.data.name);
    this.form.get('cp_name').setValue(params.data.cp_name);
    this.form.get('description').setValue(params.data.description);

    this.form.get('logoPath').setValue(params.data.logoPath);
    this.form.get('mobile').setValue(params.data.mobile);
    this.form.get('email').setValue(params.data.email);
    this.form.get('addressLine1').setValue(params.data.address1);
    this.form.get('addressLine2').setValue(params.data.address2);
    this.form.get('pincode').setValue(params.data.PIN);
    this.form.get('city').setValue(params.data.city_id);
    this.form.get('state').setValue(params.data.state_id);
    this.form.get('country').setValue(params.data.country_id);
    this.form.get('landmark').setValue(params.data.landmark);
    this.form.get('gst_number').setValue(params.data.gst_no);
    this.form.get('tin_number').setValue(params.data.tin_no);
    this.status = params.data.status == 'Y' ? true : false;
  }

  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }

  getStateCountry() {

    this.pin_code_id = this.form.value.pincode;
    //;
    this.countryService.getCountryStateByPIN(this.pin_code_id).subscribe(
      (data: any) => {
        //this.form.reset();
        if (data != undefined && data != null && data.length > 0) {
          this.state_id = data[0].state_id;
          //;
          this.countryService.getCountryByState(this.state_id).subscribe(
            (countrydata: any) => {
              //this.form.reset();
              this.countries = countrydata;
              const toSelect = this.countries.find(c => c.id == data[0].country_id);
              this.form.get('country').setValue(toSelect);

              this.filteredOptionCountry = this.form.controls.country.valueChanges
                .pipe(
                  startWith(''),
                  map(value => typeof value === 'string' ? value : value.name),
                  map(name => name ? this._filterCountry(name) : this.countries)
                );
              //; 
              this.country_id = data[0].country_id;
              this.countryService.getStateByCountry(this.country_id).subscribe(
                (statedata: any) => {
                  //this.form.reset();
                  this.states = statedata;
                  const toSelect = this.states.find(c => c.id == data[0].state_id);
                  this.form.get('state').setValue(toSelect);

                  this.filteredOptionState = this.form.controls.state.valueChanges
                    .pipe(
                      startWith(''),
                      map(value => typeof value === 'string' ? value : value.name),
                      map(name => name ? this._filterState(name) : this.states)
                    );

                }, (err) => {

                }
              )
              //; 
              this.state_id = data[0].state_id;
              this.countryService.getCityByState(this.state_id).subscribe(
                (Citydata: any) => {
                  //this.form.reset();
                  this.cities = Citydata;
                  //const toSelect = this.states.find(c => c.id == data[0].state_id);
                  // this.form.get('state').setValue(toSelect);

                  this.filteredOptionCity = this.form.controls.city.valueChanges
                    .pipe(
                      startWith(''),
                      map(value => typeof value === 'string' ? value : value.name),
                      map(name => name ? this._filterCity(name) : this.cities)
                    );

                }, (err) => {

                }
              )
            }, (err) => {

            }
          )
        } else {

        }


      }, (err) => {

      }
    )
  }

  onCountryChange(event) {
    this.countries = [];
    this.getAllCountry();
    this.getStatesByCountry(event);


    //this.form.get('pincode').setValue("");
    this.form.get('state').setValue("");
    this.form.get('city').setValue("");
  }

  getStatesByCountry(state) {
    this.state_id = state.id;
    this.countryService.getStateByCountry(this.state_id).subscribe(
      (statedata: any) => {
        //this.form.reset();
        this.states = statedata;
        // const toSelect = this.states.find(c => c.id == data[0].state_id);
        // this.form.get('state').setValue(toSelect);

        this.filteredOptionState = this.form.controls.state.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterState(name) : this.states)
          );

      }, (err) => {

      }
    )
  }

  onStateChange(event) {
    this.getCitiesByStates(event);
    this.getCountryByState(event);
    this.form.get('city').setValue("");
    //this.form.get('pincode').setValue("");
  }

  onCityChange(event){
   //this.form.get('pincode').setValue("");
  }

  getCitiesByStates(stateId) {
    this.state_id = stateId.id;
    this.countryService.getCityByState(this.state_id).subscribe(
      (citiesdata: any) => {
        this.cities = citiesdata;
        
        this.filteredOptionCity = this.form.controls.city.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCity(name) : this.cities)
          );

      }, (err) => {

      }
    )
  }

  getCountryByState(stateId) {
    this.state_id = stateId.id;
    this.countryService.getCountryByState(this.state_id).subscribe(
      (countrydata: any) => {
        //this.form.reset();
        this.countries = countrydata;
        const toSelect = this.countries.find(c => c.id == countrydata[0].id);
        this.form.get('country').setValue(toSelect);

        this.filteredOptionCountry = this.form.controls.country.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCountry(name) : this.countries)
          );

      }, (err) => {

      }
    )
  }
  displayCountry(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCountry(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.countries.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  displayState(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterState(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.states.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  displayCity(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCity(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.cities.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  closeActionPanel(){
    this.dialogRef.close();
  }
}





