
import { Component, ElementRef, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ClientService } from 'app/shared/services/client/client.service';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import * as XLSX from 'xlsx';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
export class Client {
  id: string;
  name: string;
  description: string;
  address: string;
  logoPath: string;
  mobile: string;
  email: string;
  status: string;
  created_date: string;
  createdby: number;
  modify_date: string;
  modifyby: number;
  statusString: string;
  cp_name: string;
  gst_no: string;
}
interface CustomColumn {
  possition: number;
  name: string;
  isActive: boolean;
}
/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})
export class ListComponent implements OnInit {
  filterEntity: Client;
  filterType: MatTableFilter;
  columnList: string[] = ['name', 'cp_name', 'gst_no', 'status', 'action'];
  head = [['Client Name', 'Contact Person Name', 'GSTN', 'Mobile', 'Email', 'Status']];
  public columnShowHideList: CustomColumn[] = [];
  dataSource: MatTableDataSource<Client>;

  clients: Client[];
  localStorage: any;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  dialogRef: any;
  nameSearch = false;
  contactPersonSearch = false;
  mobileSearch = false;
  emailSearch = false;
  statusSearch = false;
  gstSearch = false;
  exportedData: any[];
  constructor(private clientService: ClientService, public dialog: MatDialog,
    private snackBar: MatSnackBar) {
    // Create 100 users
    // const users = Array.from({length: 100}, (_, k) => createNewUser(k + 1));

    // Assign the data to the data source for the table to render
  }

  ngOnInit() {
    this.getClients();
    this.initializeColumnProperties();
    this.filterEntity = new Client();
    this.filterType = MatTableFilter.ANYWHERE;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getClients() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.clientService.getClientsCW(this.localStorage.userId).subscribe(
      (data: any) => {
        if (data != null) {
          this.clients = data;
          for (var i = 0; i < this.clients.length; i++) {
            if (this.clients[i].status == 'N') {
              this.clients[i].statusString = 'Inactive';
            } else {
              this.clients[i].statusString = 'Active';
            }


          }
          this.dataSource = new MatTableDataSource(this.clients);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
          return;
        }


      }, (err) => {
      }
    )
  }

  delete(id) {
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
    });

    //this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.clientService.delete(id).subscribe(
          (data: any) => {

            this.getClients();
            this.snackBar.open(MessageConstant.deleteMessage, 'Dismiss', { duration: 2000 });
          }, (err) => {

          }
        )
      }
      this.confirmDialogRef = null;
    });

  }


  openDialog(action, data): void {

    let params = {
      action: action,
      data: data
    }

    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '800px',
      data: params,
      autoFocus: false,
      disableClose: true,
      panelClass: 'app-action-dialog',
    });

    dialogRef.afterClosed().subscribe(result => {

      // this.moderate(result);
      this.getClients();
    });
  }
  viewDialog(action, data): void {
    let params = {
      action: action,
      data: data
    }

    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {

      // this.moderate(result);
      this.getClients();
    });
  }
  changeVisibility(visibleOption) {
    // visibleOption = !visibleOption;
    //nameSearch = false;
    //descSearch = false;
    if (visibleOption == 'nameSearch') {
      this.nameSearch = !this.nameSearch;
      this.filterEntity.name = '';
    }
    if (visibleOption == 'gstSearch') {
      this.gstSearch = !this.gstSearch;
      this.filterEntity.gst_no = '';
    }
    if (visibleOption == 'contactPersonSearch') {
      this.contactPersonSearch = !this.contactPersonSearch;
      this.filterEntity.cp_name = '';
    }
    if (visibleOption == 'mobileSearch') {
      this.mobileSearch = !this.mobileSearch;
      this.filterEntity.mobile = '';
    }
    if (visibleOption == 'emailSearch') {
      this.emailSearch = !this.emailSearch;
      this.filterEntity.email = '';
    }

    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = '';
    }
  }
  tableExport() {

    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var chargerModel = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = {
        'Client Name': chargerModel.name, 'Contact Person Name': chargerModel.cp_name, 'GSTIN': chargerModel.gst_no,
        'Mobile': chargerModel.mobile, 'Email': chargerModel.email, 'Status': chargerModel.statusString
      };
      this.exportedData.push(data);
    }


    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();

    XLSX.utils.book_append_sheet(workBook, workSheet, 'Client List');
    XLSX.writeFile(workBook, 'Client List.xlsx');
  }

  pdfExport() {

    var doc = new jsPDF({ // set needed dimensions for any elemen

    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var chrger = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = [chrger.name, chrger.cp_name, chrger.gst_no, chrger.mobile, chrger.email, chrger.statusString];

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,

      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('Client List Report', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not for Public use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')

    // below line for Download PDF document  
    doc.save('Client List report.pdf');

  }
  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example

    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right")
    return doc;

  }
  toggleColumn(column) {
    if (column.isActive) {
      if (column.possition > this.columnList.length - 1) {
        this.columnList.push(column.name);
      } else {
        this.columnList.splice(column.possition, 0, column.name);
      }
    } else {
      let i = this.columnList.indexOf(column.name);
      let opr = i > -1 ? this.columnList.splice(i, 1) : undefined;
    }
  }

  initializeColumnProperties() {
    this.columnList.forEach((element, index) => {
      this.columnShowHideList.push(
        { possition: index, name: element, isActive: true }
      );
    });
    // After for loop it will look like this
    //   public columnShowHideList = [
    //   { possition: 0, name: 'action', isActive: true },
    //   { possition: 1, name: 'userName', isActive: true },
    //   { possition: 2, name: 'email', isActive: true },
    //   { possition: 3, name: 'contactNo', isActive: true },
    //   { possition: 4, name: 'address', isActive: true }
    // ];
  }
}

