import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ClientCrudComponent } from './client-crud/client-crud.component';
import { TABS_DEMO_ROUTES } from './client-crud/routes';
import { ClientMenuComponent } from './client-menu/client-menu.component';
import { ClientModuleMappingModule } from './client-module-mapping/client-moduleMapping.module';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : ClientCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    },
	{
        path : 'clientMenuMappings',component : ClientMenuComponent,canActivate: [AuthGuard]
    },
    
    {
        path: 'clientModuleMapping',
        loadChildren: () => import('./client-module-mapping/client-moduleMapping.module').then(m => m.ClientModuleMappingModule),
        data: { title: 'Client Module Mapping', titleI18n: 'clientModule' },
    },
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ClientRoutingModule{}